import React, { useState, useEffect } from 'react';
import '../styles/Header.css';

const Header = () => {
  /* ===========================================
     1) STATE
  =========================================== */
  const [activeSection, setActiveSection] = useState('');
  const [scrolled, setScrolled] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);

  /* ===========================================
     2) SCROLL & SECTION-HIGHLIGHT HANDLING
  =========================================== */
  useEffect(() => {
    const handleScroll = () => {
      const sections = document.querySelectorAll('section');
      const scrollPosition = window.scrollY + 200; // 200 offset for highlighting earlier

      sections.forEach((section) => {
        const start = section.offsetTop;
        const end = section.offsetTop + section.offsetHeight;
        if (scrollPosition >= start && scrollPosition < end) {
          setActiveSection(section.id);
        }
      });

      // Toggle 'scrolled' state for styling
      setScrolled(window.scrollY > 50);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  /* ===========================================
     3) MENU TOGGLE FOR MOBILE
  =========================================== */
  const toggleMenu = () => {
    setMenuOpen((prev) => !prev);
  };

  /* ===========================================
     4) RENDER
  =========================================== */
  return (
    <header className={`site-header ${scrolled ? 'scrolled' : ''}`}>
      <div className="header-content">
        <h1>The New American Dream</h1>
        <button
          type="button"
          className="menu-toggle"
          onClick={toggleMenu}
          aria-label={menuOpen ? 'Close menu' : 'Open menu'}
          aria-expanded={menuOpen}
          aria-controls="primary-nav"
        >
          
        </button>
      </div>

      <nav 
        id="primary-nav" 
        className={`primary-nav ${menuOpen ? 'open' : ''}`} 
        aria-hidden={!menuOpen}
      >
        <ul>
          <li>
            <a
              href="#overview"
              className={activeSection === 'overview' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Overview
            </a>
          </li>
          <li>
            <a
              href="#principles"
              className={activeSection === 'principles' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Core Principles
            </a>
          </li>
          <li>
            <a
              href="#tools"
              className={activeSection === 'tools' ? 'active' : ''}
              onClick={() => setMenuOpen(false)}
            >
              Interactive Tools
            </a>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;